<template>
    <div v-if="visible">
        <highcharts class="hc" :options="chartOptions" :updateArgs="updateArgs"	></highcharts>
    </div>
</template>

<script>
    import store from "./../chartDataStore";
    var scrollMonitor = require("scrollmonitor");
    const containerMonitor = scrollMonitor.createContainer(document.body);

    export default {
        props: ["id"],
        data: function () {
            return {
                visible: true,
                updateArgs: [true, false, false],
                xCounter: store.getters.getXCounter(this.id),
                chartOptions: store.getters.getChartOptions(this.id)
            };
        },
        mounted() {
            const elementWatcher = containerMonitor.create(this.$el.querySelector('.hc'));

            elementWatcher.enterViewport(() => {
                this.visible = true;
            });
            elementWatcher.exitViewport(() => {
                this.visible = false;
            });
        }
    };
</script>

<style>
    .hc {       
        float:left;
    }
</style>
